$tablet-width: 1023px;
$tablet-width-only: 768px;
$mobile-width: 767px;

$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;

$color-black: #1d1613;
$color-brown: #a87b62;
$color-grey: #9b9997;


@mixin list-reset {
  margin: 0;
  padding: 0;

  list-style: none;
}

@mixin garamond-font-family {
  font-family: "Cormorant Garamond", "Times New Roman", serif;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  opacity: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.no-scroll {
  height: 100vh;
  overflow: hidden;
}
